const namespace = 'http://www.w3.org/2000/svg';

let workspace = document.getElementById('workspace');

function createElement(name, attributes) {
    let element = document.createElementNS(namespace, name);
    for (let name of attributes) {
        element.setAttribute(name, attributes[name]);
    }
    return element;
}

function createNode(title) {
    let group = createElement('g', {});

    let background = createElement('rect', {'class': 'node-bg', 'rx': 15, 'ry': 15});
    group.appendChild(background);

    let header = createElement('path', {'class': 'node-header'});
    group.appendChild(header);

    let titleText = createElement('text', {'class': 'node-title'});
    titleText.textContent = title;
    group.appendChild(titleText);

    let outline = createElement('rect', {'class': 'node-outline', 'rx': 15, 'ry': 15});
    group.appendChild(outline);

    let props = createPropsGroup(['Test property', 'Other property']);
    group.appendChild(props);

    return group;
}

function createPropsGroup(props) {
    let group = createElement('g', {});

    for (let i = 0; i < props.length; i++) {
        let propCircle = createElement('circle', {
            'class': 'node-outlet',
            'r': 5
        });
        group.appendChild(propCircle);

        let propText = createElement('text', {
            'class': 'node-property'
        });
        propText.textContent = props[i];
        group.appendChild(propText);
    }
    console.dir(group);
    return group;
}

let nodeX;
let nodeY;
let dragOffsetX;
let dragOffsetY;

let group = createNode("Ridiculously long title for this node");
positionNode(group, 200, 50, 300, 200);
workspace.appendChild(group);

group.onmousedown = function (event) {
    console.dir(event);
    let bounds = group.getBoundingClientRect();
    dragOffsetX = event.x - nodeX;
    dragOffsetY = event.y - nodeY;
    console.log(dragOffsetX, dragOffsetY);
    workspace.addEventListener('mousemove', onMouseMove);
    return false;
};

workspace.onmouseup = function (event) {
    workspace.removeEventListener('mousemove', onMouseMove);
    return false;
};

function positionNode(node, x, y, width, height) {
    nodeX = x;
    nodeY = y;
    let background = group.querySelector('.node-bg');
    background.setAttribute('x', x);
    background.setAttribute('y', y);
    background.setAttribute('width', width);
    background.setAttribute('height', height);

    let header = group.querySelector('.node-header');
    header.setAttribute('d', `M ${x},${y + 15} a 15 15 0 0 1 15,-15 h ${width - 30} a 15 15 0 0 1 15,15 v 15 h -${width} v -15`);

    let titleText = group.querySelector('.node-title');
    titleText.setAttribute('x', x + 15);
    titleText.setAttribute('y', y + 20);
    titleText.setAttribute('clip-path', `polygon(0px 0px, ${width - 30}px 0px, ${width - 30}px 30px, 0px 30px)`);

    let outline = group.querySelector('.node-outline');
    outline.setAttribute('x', x);
    outline.setAttribute('y', y);
    outline.setAttribute('width', width);
    outline.setAttribute('height', height);

    let propCircles = group.querySelectorAll('.node-outlet');
    console.dir(propCircles);
    for (let i = 0; i < propCircles.length; i++) {
        propCircles[i].setAttribute('cx', x + width);
        propCircles[i].setAttribute('cy', y + 60 + (i * 30));
    }

    let propTexts = group.querySelectorAll('.node-property');
    for (let i = 0; i < propTexts.length; i++) {
        propTexts[i].setAttribute('x', (x + width - 15).toString());
        propTexts[i].setAttribute('y', y + 60 + (i * 30) + 5);
    }
}

function onMouseMove(event) {
    positionNode(group, event.x - dragOffsetX, event.y - dragOffsetY, 300, 200);
    return false;
}
